  font-size: 14px;
  font-weight: 500;
  line-height: 164%; /* 164.286% */
}
.teacher__descr__right_main_bottom_descr {
  color: #292c32;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.5; /* 150% */
}
.teacher__descr_avatar {
  width: 130px;
  height: 130px;
}
.teacher__descr_stars {
  margin-top: 17px;
  width: 83px;
  justify-content: space-between;
  display: flex;
  gap: 4px;
}
.teacher__descr_star {
  fill: #cccccc;
  width: 13px;
  height: 13px;
}
.teacher__descr_star_filled {
  fill: rgb(255, 196, 82);
}
.teacher__descr_recommend {
  color: #fff;
  font-size: 15px;
  font-weight: 600;
  line-height: 150%;
